<template>
  <v-toolbar dark data-app fixed color="indigo accent-4">
    <v-toolbar-title><v-icon class="pr-3">group</v-icon>MY STUDENTS</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items class="hidden-sm-and-down">
      <v-btn to="/add" flat><v-icon class="pr-1">add</v-icon>ADD STUDENTS</v-btn>
      <v-btn @click="logout" flat><v-icon class="pr-1">logout</v-icon>LOGOUT</v-btn>
    </v-toolbar-items>
    <v-toolbar-items class="hidden-md-and-up">
       <v-btn to="/add" fab dark small color="green">
           <v-icon dark>add</v-icon>
        </v-btn>
       <v-btn fab dark @click="logout" small color="red">
           <v-icon dark>logout</v-icon>
        </v-btn>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
import firebase from 'firebase';

export default {
  data(){return{
    isLoggedIn: false,
    currentUser: false
  }},
  methods:{
    logout: function(){
      if(confirm("Are you sure want to logout !")){
      firebase.auth().signOut().then(
        () => {
          this.$router.push('/login')
        }
      );
      }
    }
  }
  
}
</script>



