<template>
<div id="addstudent">
    <toolbar></toolbar>
<div class="pt-5">
     <v-container fluid="" fill-height>
        <v-layout align-center justify-center>
          <v-flex xs11 sm5 md3>
  <v-hover>
    <v-card
      slot-scope="{ hover }"
      :class="`elevation-${hover ? 18 : 28}`"
      class="br"
    >
     <div class="text-xs-center py-2">
        <img width="100" src="../assets/logo.png"/>
        <h3>NEW STUDENT</h3>
     </div>
      <v-form v-model="valid" @submit.prevent="savestudent">
        <v-text-field
          v-model="name"
          class="mx-3"
          label="NAME"
          :rules="namerules"
          box
          outline
          required
          flat
          clearable
          background-color="blue"
        ></v-text-field>
        <v-text-field
          v-model="regno"
          class="mx-3"
          label="REG-NO"
          :rules="regrules"
          box
          outline
          required
          flat
          clearable
          background-color="blue"
        ></v-text-field>
        <v-text-field
          v-model="department"
          class="mx-3"
          label="DEPARTMENT"
          :rules="deptrules"
          box
          outline
          required
          flat
          clearable
          background-color="blue"
        ></v-text-field>
        <v-menu
          ref="menu1"
          v-model="menu1"
          :close-on-content-click="false"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          attach=""
          full-width
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="computedDateFormatted"
              label="Date"
              class="mx-3"
              box
              outline
              flat
              readonly=""
              clearable
              background-color="blue"
              :rules="dobrules"
              @blur="date = parseDate(dateFormatted)"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date" color="blue" header-color="blue" @input="menu1 = false"></v-date-picker>
        </v-menu>
        <v-text-field
          v-model="partno"
          class="mx-3"
          label="PARENTS NO"
          :error-messages='numMatchError2'
          box
          counter="10"
          outline
          required
          flat
          clearable
          background-color="blue"
        ></v-text-field>
        <v-text-field
          v-model="studno"
          class="mx-3"
          label="STUDENT NO"
          :error-messages='numMatchError1'
          counter="10"
          box
          outline
          required
          flat
          clearable
          background-color="blue"
        ></v-text-field>

        <div class="text-xs-center pb-3 pt-0">
            <v-btn color="red"
            outline=""
            round
            to="/"
             >CANCEL
             <v-icon right>cancel</v-icon></v-btn>      
             <v-btn
                :loading="loading3"
                :disabled="!valid"
                color="blue"
                outline
                type="submit"
                round
                @click="loader = 'loading3'"
                >
                CREATE
                <v-icon right dark>cloud_upload</v-icon>
                </v-btn>
        </div>
      </v-form>
      <v-divider></v-divider>
       <div class="text-xs-center py-3">
           <h4>@skct</h4> 
       </div>
     </v-card>
    </v-hover>
    </v-flex>
   </v-layout>
   </v-container>
</div>
</div>
</template>

<style>
  
   .br{
        border-radius:20px;
   }
</style>

<script>
import db from './firebaseinit'
import Toolbar from './Toolbar.vue'
import { type } from 'os';
  export default {
    components:{
        Toolbar
    },
    data: vm => ({
                date: new Date().toISOString().substr(0, 10),
                dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
               menu1: false,
               name: null,
               loader:null,
               loading3:false,
               regno: null,
               department: null,
               partno: null,
               studno: null,
               valid: false,
               namerules: [
                v => !!v || 'Name is required',
                ], 
                regrules: [
                v => !!v || 'Reg-no is required',
                ], 
                deptrules: [
                v => !!v || 'Department is required',
                ], 
                dobrules: [
                v => !!v || 'DOB is required',
                ],  
        }),
        methods:{
            savestudent() {
                db.collection('mystudents').add({
                    name: this.name,
                    regno: this.regno,
                    department: this.department,
                    dob: this.computedDateFormatted,
                    partno: this.partno,
                    studno: this.studno
                })
                .then(docref => this.$router.push('/'))
                .catch(error => console.log(err))
            },
             formatDate (date) {
                    if (!date) return null

                    const [year, month, day] = date.split('-')
                    return `${month}/${day}/${year}`
                },
             parseDate (date) {
                    if (!date) return null

                    const [month, day, year] = date.split('/')
                    return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
                }
        },
        
        computed: {
        computedDateFormatted () {
            return this.formatDate(this.date)
        },
          numMatchError1 () { 
            return (!!this.studno && !isNaN(this.studno) && this.studno.length === 10 ) ? '' : '<div style="color:red">*Required</div>'
        },
         numMatchError2 () { 
            return (!!this.partno && !isNaN(this.partno) && this.partno.length === 10 ) ? '' : '<div style="color:red">*Required</div>'
        },
        },

        watch: {
        date (val) {
            this.dateFormatted = this.formatDate(this.date)
        },
        loader () {
        const l = this.loader
        this[l] = !this[l]

        setTimeout(() => (this[l] = false), 10000)

        this.loader = null
      }
        },
    
      }
</script>

<style>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>




