<template>
   <div>
       <toolbar></toolbar>
       <v-flex pt-4>
       <v-toolbar  class="pt-5">  <v-text-field
          v-model="search"
          class=" pb-0"
          label="SEARCH"
          box
          outline
          required
          single-line=""
          prepend-inner-icon="search"
          background-color="blue"
        ></v-text-field></v-toolbar>
       </v-flex>
        <v-list>
            <v-flex pt-3 pb-0>
        
            </v-flex>
        <v-list-tile
         v-for="stud in filterdstud" v-bind:key="stud.id"
         v-bind:to="{name:'stud-profile', params: { regno : stud.regno }}"
        class="pt-0 pb-1 "
        >
          <v-list-tile-action>
            <img width="30" src="../assets/logo.png" />
          </v-list-tile-action>
          <v-list-tile-content>
           <h2> <v-list-tile-title>{{ stud.name }}</v-list-tile-title></h2>
          </v-list-tile-content>  
           <v-spacer></v-spacer>
           <v-icon>chevron_right</v-icon>
        </v-list-tile>
      </v-list>
   </div>
 </template>

<script>
import db from './firebaseinit'
import Toolbar from './Toolbar.vue'
export default {
    components:{
        Toolbar
    },
    data(){
        return{
            mystudents:[],
            search:''
        }},
        created(){
                db.collection('mystudents').orderBy('name').get().then(
                    querysnapshot =>(
                    querysnapshot.forEach(doc => {
                     const data={
                         'id' : doc.id,
                         'regno' : doc.data().regno,
                         'name' : doc.data().name,
                         'department' : doc.data().department,
                         'dob' : doc.data().dob,
                         'parentsno' : doc.data().partno,
                         'studentno' : doc.data().studno,
                         'partno' : 'tel:' + doc.data().partno,
                         'studno' : 'tel:' + doc.data().studno
                     }
                     this.mystudents.push(data)
                    })
                )
                   
                )
        },
        computed:{
            filterdstud:function(){
                return this.mystudents.filter((stud) => {
                    return stud.name.match(this.search) || stud.regno.match(this.search);
                });
            }

        }
}
</script>
<style>
 .card{
     border-radius: 5px;
 }
</style>




