<template>
<div id="landing-page">
<div class="pt-3">
     <v-container fluid="" fill-height>
        <v-layout align-center justify-center>
          <v-flex xs11 sm5 md3>
  <v-hover>
    <v-card
      slot-scope="{ hover }"
      :class="`elevation-${hover ? 30 : 14}`"
      class="br"
    >
     <div class="text-xs-center py-2">
        <img width="150" src="../assets/logo.png"/>
        <h1>LOGIN</h1>
     </div>
      <v-form v-model="valid" @submit.prevent="submit()">
        <v-text-field
          v-model="email"
           class="mx-3"
          :rules="emailRules"
          label="E-mail"
          box
          outline
          required
          flat
          clearable
          background-color="blue"
        ></v-text-field>
        <v-text-field
          class="mx-3"
          v-model="password"
          :rules="passwordRules"
          label="PASSWORD"
          box
          type="password"
          required
          flat
          clearable
          background-color="blue"
          width="20px"
          outline
        ></v-text-field>
        <div class="text-xs-center pb-3 pt-0">
            <v-btn color="blue"
            :disabled="!valid"
            outline=""
            round
            large=""
            type="submit">submit</v-btn>
        </div>
      </v-form>
      <v-divider></v-divider>
       <div class="text-xs-center py-3">
           <h4>@skct</h4> 
       </div>
     </v-card>
    </v-hover>
    </v-flex>
   </v-layout>
   </v-container>
</div>
</div>
</template>

<style>
  
   .br{
        border-radius:20px;
   }
    #landing-page {
    background: rgba(51,119,255,1);
    background: -moz-linear-gradient(45deg, rgba(51,119,255,1) 0%, rgba(51,119,255,1) 47%, rgba(15,86,255,1) 64%, rgba(5,76,255,1) 69%);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(51,119,255,1)), color-stop(47%, rgba(51,119,255,1)), color-stop(64%, rgba(15,86,255,1)), color-stop(69%, rgba(5,76,255,1)));
    background: -webkit-linear-gradient(45deg, rgba(51,119,255,1) 0%, rgba(51,119,255,1) 47%, rgba(15,86,255,1) 64%, rgba(5,76,255,1) 69%);
    background: -o-linear-gradient(45deg, rgba(51,119,255,1) 0%, rgba(51,119,255,1) 47%, rgba(15,86,255,1) 64%, rgba(5,76,255,1) 69%);
    background: -ms-linear-gradient(45deg, rgba(51,119,255,1) 0%, rgba(51,119,255,1) 47%, rgba(15,86,255,1) 64%, rgba(5,76,255,1) 69%);
    background: linear-gradient(45deg, rgba(51,119,255,1) 0%, rgba(51,119,255,1) 47%, rgba(15,86,255,1) 64%, rgba(5,76,255,1) 69%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3377ff', endColorstr='#054cff', GradientType=1 );
    background-size: cover;
    min-height: 100vh;
    background-attachment: scroll;
  } 

</style>

<script>
import firebase from 'firebase';

  export default {
      data: () => ({
       
        password: '',
        passwordRules: [
          v => !!v || 'password is required',
        ], 
        valid:true,
        email:'',
        emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@skct.edu.in/.test(v) || 'E-mail must be valid'
        ], 
      }),
      methods:{
          submit:function(){  
            firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
              user => {
                this.$router.push('/');
              },
              err => {
                alert(err.message);
              }
            );
              }
              
          }
      }
</script>






