
<template>
<div id="profile">
    <toolbar></toolbar>
<div class="pt-5">
     <v-container fluid="" fill-height>
        <v-layout align-center justify-center>
          <v-flex xs11 sm5 md3>
  <v-hover>
    <v-card
      slot-scope="{ hover }"
      :class="`elevation-${hover ? 18 : 28}`"
      class="card"
    >
     <div class="text-xs-center py-2">
        <img width="100" src="../assets/logo.png" />
        <h2 class="py-0" style="color:blue">{{ name.toUpperCase() }}</h2>
        <h2 class="py-2">{{ regno }}</h2>
        <h2 class="pb-2">{{ department.toUpperCase() }}</h2>
        <h2 class="pb-2">{{ dob }}</h2>
        <h2 class="pb-2">Parents no:<a :href="partno" style="text-decoration:none"> {{ parents }} </a></h2>
        <h2 class="pb-2">Student no:<a :href="studno" style="text-decoration:none"> {{ student }} </a></h2>
        <v-btn outline round @click="deletestudent" color="red">DELETE</v-btn>
        <v-btn color="blue" round to="/" outline>BACK</v-btn>
     </div>
      <v-divider></v-divider>
       <div class="text-xs-center py-3">
           <h4>@skct</h4> 
       </div>
     </v-card>
    </v-hover>
    </v-flex>
   </v-layout>
   </v-container>
</div>
</div>
</template>

<style>
  
   .card{
        border-radius:15px;
   }
</style>

<script>
import db from './firebaseinit'
import Toolbar from './Toolbar.vue'
import { watch } from 'fs';
  export default {
      components:{
        Toolbar
    },
      data(){
          return{
              name: '',
              regno:'',
              department: '',
              dob: '',
              partno: '',
              studno: '',
              parents: '',
              student : ''
      }
      },
      beforeRouteEnter(to, from, next){
          db.collection('mystudents').where('regno','==', to.params.regno).get().then(
              querysnapshot =>{querysnapshot.forEach(doc => {
                 next( vm => {
                     
                        vm.regno = doc.data().regno,
                        vm.name = doc.data().name,
                        vm.department = doc.data().department,
                        vm.dob = doc.data().dob,
                        vm.parents = doc.data().partno,
                        vm.student = doc.data().studno,
                        vm.partno = 'tel:' + doc.data().partno,
                        vm.studno = 'tel:' + doc.data().studno
                 })
              })
      })
      },
      watch: {
          '$route' : 'fetchdata'
      },
      methods:{
          fetchdata: function(){
              db.collection('mystudents').where('regno','==', this.$route.params.regno).get().then(
                 querysnapshot =>{
                    querysnapshot.forEach(doc => {
                    
                        this.regno = doc.data().regno,
                        this.name = doc.data().name,
                        this.department = doc.data().department,
                        this.dob = doc.data().dob,
                        this.parents = doc.data().partno,
                        this.student = doc.data().studno,
                        this.partno = 'tel:' + doc.data().partno,
                        this.studno = 'tel:' + doc.data().studno

                    })
                 }
              )
          },
         deletestudent(){
          if(confirm("are you sure want to delete")){
              db.collection('mystudents').where('regno','==', this.$route.params.regno).get().then(
                 querysnapshot =>{
                    querysnapshot.forEach(doc => {
                      doc.ref.delete()
                      this.$router.push('/')

                    })
                 }
              )}
      }
   },
  }  
</script>






